/* FAVOURITES SELECTS */
.FavOuterShell {
  z-index: 5;
  position: sticky;
  top: 2em;
  min-height: 100vh;
  background: #e09b04;
  color: #000;
  padding: 1em;
  margin: 0 2em 2em 1em;
}

.FavOuterContent {
  color: #ec0101;
  margin: 1em;
  padding: 1em 2em;
  border: 1px solid #fff;
}

.FavOuterHeader {
  font-size: 2em;
  font-weight: 500;
  letter-spacing: 4px;
  line-height: 2;
  color: #000;
}

.TableHead {
  color: #000 !important;
  line-height: 2;
}

.dumpsterBtn {
  cursor: pointer;
  color: #ec0101;
}
